import React from 'react'
import PropTypes from 'prop-types'
import Todo from '../components/Todo'
import '../style.css'

class Todos extends React.Component {
  constructor(props) {
    super(props);
    this.state = {todos: []};
  }
  componentDidMount(){
    const todos = [
      {text: "Take out trash"},
      {text: "Ask out that girl"},
      {text: "Sleep"}
    ];
    this.setState({todos}); // update state w/ array
  }

  render(){
    return (
        <div className='card'>
          <div className='card-body'>
            {this.state.todos.map(item => <Todo text={item.text} />) }
          </div>
        </div>

          );
  }
}

export default Todos;
